<template>
  <div class="excel-upload">
    <NavBar
      :to-display="{ selectionList: true }"
      :fields="{'nameField':'schoolName', 'id':'schoolId'}"
    />
    <div class="main-content-padding">
      <h2>Excel file upload</h2>
      <br>
      <br>
      <div class="upload-box">
        <div v-if="!isCompleted">
          <h4>Select file to upload:</h4>
          <br>
          <input
            id="file-container"
            ref="file"
            type="file"
            accept=".xls,.xlsx,.csv"
            @change="handleFileUpload"
          >
          <br>
          <br>
          <br>
          <button @click="uploadExcelSheet">Submit</button>
        </div>
        <div v-else>
          <br>
          <br>
          <b style="color:purple">Upload complete!</b>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue';
import teacherService from '@/services/teacher-service.js';
import logger from '@/services/logger.js';

export default {
  name: 'ExcelUpload',
  components: {
    NavBar
  },
  data() {
    return {
      file: '',
      uploadCompleted: false
    };
  },
  computed: {
    isCompleted() {
      return this.uploadCompleted;
    }
  },
  methods: {
    handleFileUpload: function() {
      this.file = this.$refs.file.files[0];
    },
    uploadExcelSheet: async function() {
      this.getBase64(this.file);
      // await teacherService.uploadExcelFile(base64File);
    },
    getBase64: function(file) {
      var reader = new FileReader();
      reader.readAsDataURL(file);
      let theOtherThis = this._data;
      reader.onload = async function() {
        let parsedResult = reader.result.split(',')[1];
        let response = await teacherService.uploadExcelFile(parsedResult);

        if (response.data) {
          theOtherThis.uploadCompleted = true;
          logger.debug(response.data);
        } else {
        }
        // return reader.result;
      };
      reader.onerror = function(error) {
        logger.debug('Error: ', error);
        return false;
      };
    },
    setUploadCompleted: function(condition) {
      this.uploadCompleted = condition;
    }
  }
};
</script>

<style>
.upload-box {
  box-shadow: 2px 2px 25px -6px black;
  display: inline-block;
  padding: 10px 40px 40px 40px;
  border-radius: 15px;
}

#file-container {
  box-shadow: 0px 0px 5px -1px black;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(240, 240, 240, 0.671);
}

.excel-upload button {
  transition: 0.2s;
  border: none;
  padding: 0.5rem 2rem 0.5rem 2rem;
  border-radius: 25px;
  font-size: 14pt;
  margin: 10px;
  box-shadow: 2px 2px 15px -5px black;
  outline: 0;
  cursor: pointer;
}

.excel-upload button:active {
  transition: 0.05s;
  transform: translateY(4px);
}

.main-content-padding {
  padding: 120px;
}
</style>
