<template>
  <div class="student-managment">
    <NavBar
      :fields="{'nameField':'schoolClassName', 'id':'classId'}"
      :to-display="{ 'selectionList': true, 'contactLink': true, 'tosLink': false }"
    />
    <div class="student-manage-content">
      <h4>User Managment Tool</h4>
      <div 
        class="students-list"
      >
        <div class="item">

          <div class="manager-grid-item">
            <b>Student Name</b>
          </div>
          <div class="manager-grid-item">
            <b>Class</b>
          </div>
          <div class="manager-grid-item last-one">
            <b>Delete</b>
          </div>
        </div>
        <div 
          v-if="students && students.length > 0" 
          class="real-data-students"
        >
          <div
            v-for="student in students" 
            :key="student.docId"
            class="item real-data"
          >
            <div class="manager-grid-item">
              {{ student.studentName }}
            </div>
            <div class="manager-grid-item">
              {{ student.studentClass }}
            </div>
            <div class="manager-grid-item last-one">
              <span 
                class="delete-btn" 
                @click="deleteStudent(student.docId)"
              >Delete Studnet</span>
            </div>
          </div>
        </div>
        <div 
          v-else 
          class="no-data"
        >
          <div class="item">No students in associated to you in this school.</div>
        </div>
      </div>
    </div>
  </div></template>

<script>
import logger from '@/services/logger.js';
import NavBar from '@/components/NavBar.vue';
import teacherService from '../services/teacher-service';

export default {
  name: 'StudentManagment',
  components: { NavBar },
  data() {
    return {
      students: null
    };
  },
  created() {
    this.getDataForManager();
  },
  methods: {
    getDataForManager: async function() {
      let token = this.$store.getters.getToken;
      let response = await teacherService.getStudentDataForManagmentTool(token);
      logger.debug(response);
      this.students = response.data.students;
    },
    deleteStudent: async function(studentDocId) {
      let doubleCheck = confirm(
        'Are you sure you want to delete this student?'
      );
      if (doubleCheck) {
        logger.debug(studentDocId);
        let token = this.$store.getters.getToken;
        let success = await teacherService.deleteStudentByDocId(
          token,
          studentDocId
        );
        logger.debug(success);
        if (success) {
          window.location.reload();
        }
      } else {
      }
    }
  }
};
</script>

<style>
.students-list {
  display: grid;
  margin-top: 100px;
}

.item {
  display: inline-flex;
  margin: 0 auto;
}

.real-data .manager-grid-item {
  border-top: 0px;
  font-size: 12pt;
}

.real-data-students {
  display: grid;
}

button {
  padding: 5px;
}

.manager-grid-item {
  border-bottom: 1px solid;
  border-top: 1px solid;
  border-left: 1px solid;
  padding: 10px;
  font-size: 14pt;
  width: 200px;
}

.last-one {
  border-right: 1px solid;
}

.delete-btn {
  background-color: rgb(83, 83, 83);
  padding: 5px;
  color: white;
  border-radius: 10px;
  transition: 0.3s;
  cursor: pointer;
}

.delete-btn:hover {
  transition: 0.3s;
  background-color: rgb(143, 21, 21);
  border-radius: 0px;
}

.no-data {
  padding: 10px;
  font-style: italic;
}
</style>
