<template>
  <div class="json-upload">
    <NavBar
      :to-display="{ selectionList: false }"
    />
    <div class="json-upload-content">
      <h3>JSON Upload</h3>
      <br>
      <br>
      <div
        v-if="!isUpload"
        class="content"
      >
        <span
          class="start-btn"
          @click="startJsonUpload"
        >Start JSON upload?</span>
      </div>
      <div v-else>
        <span>JSON successfully uploaded!</span>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import teacherService from '../services/teacher-service';

export default {
  name: 'JsonUpload',
  components: { NavBar },
  data() {
    return {
      isUpload: false
    };
  },
  methods: {
    startJsonUpload: async function() {
      let res = await teacherService.uploadJsonFile();
      console.log(res);
      if (res.data.success) {
        console.log('Great success!');
        this.isUpload = true;
      }
    }
  }
};
</script>

<style>
.json-upload-content {
  padding-top: 120px;
}

.start-btn {
  background-color: rgb(83, 83, 83);
  border-radius: 10px;
  padding: 10px;
  transition: 0.5s;
  color: white;
  cursor: pointer;
}

.start-btn:hover {
  box-shadow: 0px 0px 5px 1px rgb(114, 114, 114);
  background-color: rgb(95, 91, 116);
}
</style>
